@import "../../style/colors.scss";
@import "../../style/metrics.scss";

.footer-container {
    display: flex;
    justify-content: center;
    background-color: $primary-light;
    width: inherit;
}

.footer-inner-container{
}

.footer-left-box {
    display: flex;
    flex-direction: row;
}

.footer-question {
    color: $secondary;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 28px;
}

.footer-info {
    color: $title;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 19px;
    margin-top: 1rem;
    margin-bottom: 0px;
}

.footer-info-icon {
    color: $secondary;
    font-size: 18px;
    padding: 0px;
    width: 30px;
}

.footer-left-container {
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.footer-right-container {
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.footer-icon {
    width: 90px;
    height: auto;
}

.footer-signiture {
    color: $title;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 14px;
}

.footer-left-followMe-container {
    margin-top: 20px;
}

.footer-question.follow-me {
    font-size: 16px;
    margin-bottom: 10px;
}

.socialContainer.footer {
    display: flex;
    justify-content: space-between;
    width: 200px;
}

.footer-signiture-container{
    text-align: center;
}

.footer-heart-icon {
    color: $title;
    font-size: 12px;
    padding: 0px;
    padding-left: 6px;
    padding-right: 5px;
}



@media only screen and (max-width: $mobileScreen) {
    .footer-container{

    }
    .footer-icon{
        display: none;
    }

    .footer-question{
        font-size: 20px;
    }

    .footer-question.follow-me{
        font-size: 20px;
    }

    .footer-info{
        font-size: 16px;
    }

    .footer-left-followMe-container p{
        font-size: 16px;
    }

    .footer-info-icon{
        padding: 0px;
        padding-right: 20px;
    }

    .footer-left-container,
    .footer-right-container{
        justify-content: flex-start;
    }

    .footer-signiture-container{
        display: flex;
        justify-content: center;
    }

    .footer-signiture{
        width: 80%;
        font-size: 10px;
    }

    .socialContainer.footer i {
        font-size: 25px;
        padding: 0px;
        padding-right: 20px;
    }

}